import React, { useState, useRef, useEffect } from 'react';
import { Container, Row, Col, Card } from 'react-bootstrap';
import { ImageOff, ExternalLink } from 'lucide-react';
import { useData } from '../components/DataLoader';
import './Team.css';
import { useLanguage } from '../components/LanguageProvider';

const getInitials = (name) => {
  return name
    .split(' ')
    .map(word => word[0])
    .join('')
    .toUpperCase();
};

const getColorFromName = (name) => {
  const colors = [
    '#FF6B6B', '#4ECDC4', '#45B7D1', '#96CEB4', 
    '#FFEEAD', '#D4A5A5', '#9B59B6', '#3498DB'
  ];
  const index = name.split('').reduce((acc, char) => acc + char.charCodeAt(0), 0);
  return colors[index % colors.length];
};

const TeamMemberCard = ({ member, isExpanded, onToggle }) => {
  const descriptionRef = useRef(null);
  const [hasOverflow, setHasOverflow] = useState(false);
  const [imageLoaded, setImageLoaded] = useState(false);
  const [imageError, setImageError] = useState(false);
  const { language } = useLanguage();

  useEffect(() => {
    const checkOverflow = () => {
      if (descriptionRef.current) {
        const element = descriptionRef.current;
        const hasOverflow = element.scrollHeight > element.clientHeight + 5;
        setHasOverflow(!isExpanded && hasOverflow);
      }
    };

    checkOverflow();
    
    const observer = new ResizeObserver(() => {
      checkOverflow();
    });

    if (descriptionRef.current) {
      observer.observe(descriptionRef.current);
    }

    return () => observer.disconnect();
  }, [member.description, language, isExpanded]);

  const handleCardClick = (e) => {
    if (hasOverflow || isExpanded) {
      onToggle();
    }
  };

  const handleReadMoreClick = (e) => {
    e.stopPropagation();
    onToggle();
  };

  const getImageUrl = (url) => {
    if (url && url.includes('drive.google.com')) {
      if (url.includes('/file/d/')) {
        const fileId = url.split('/file/d/')[1].split('/')[0];
        return `https://drive.google.com/thumbnail?id=${fileId}&sz=s512`;
      }
    }
    return url;
  };

  const handleImageLoad = () => {
    setImageLoaded(true);
    setImageError(false);
  };

  const handleImageError = () => {
    setImageError(true);
    setImageLoaded(false);
  };

  return (
    <Card 
      className={`team-card ${isExpanded ? 'expanded' : ''}`}
      onClick={handleCardClick}
    >
      <div className="team-overlay"></div>
      <Card.Body>
        <div className="team-profile-container">
          {member.image_url ? (
            <>
              {!imageLoaded && !imageError && (
                <div className="team-loading-placeholder">
                  Loading...
                </div>
              )}
              <img
                src={getImageUrl(member.image_url)}
                alt={member.nom}
                className={`team-profile-image ${imageLoaded ? 'visible' : 'hidden'}`}
                onLoad={handleImageLoad}
                onError={handleImageError}
                style={{ 
                  display: imageError ? 'none' : 'block',
                  opacity: imageLoaded ? 1 : 0,
                  transition: 'opacity 0.3s ease'
                }}
              />
              {imageError && (
                <div
                  className="team-initials-circle error-state"
                  style={{ backgroundColor: getColorFromName(member.nom) }}
                >
                  <ImageOff size={32} />
                </div>
              )}
            </>
          ) : (
            <div
              className="team-initials-circle"
              style={{ backgroundColor: getColorFromName(member.nom) }}
            >
              {getInitials(member.nom)}
            </div>
          )}
        </div>
        <Card.Title>
          {member.personal_link ? (
            <a 
              href={member.personal_link}
              onClick={(e) => e.stopPropagation()} 
              className="team-link"
              target="_blank"
              rel="noopener noreferrer"
            >

            <span style={{ whiteSpace: 'nowrap' }}>
              {member.nom}
              <ExternalLink className="inline-block" size={14} />
            </span>
            </a>
          ) : member.nom}
        </Card.Title>
        <Card.Subtitle className="text-muted">{member.role}</Card.Subtitle>
        {member.description && (
          <div className="team-description-container">
            <div 
              ref={descriptionRef}
              className={`team-description-fade ${!isExpanded && hasOverflow ? 'has-fade' : ''}`}
              style={{ 
                maxHeight: isExpanded ? 'none' : '80px',
                overflow: 'hidden'
              }}
            >
              <Card.Text>
                {member.description}
              </Card.Text>
            </div>
            {hasOverflow && !isExpanded && (
              <button
                className="team-read-more-btn"
                onClick={handleReadMoreClick}
              >
                {language !== 'fr' ? 'Read more' : 'Lire la suite'}
              </button>
            )}
          </div>
        )}
      </Card.Body>
    </Card>
  );
};

function Team() {
  const { data, loading, error } = useData();
  const [expandedMember, setExpandedMember] = useState(null);
  const { language } = useLanguage();

  if (loading) return <Container className="my-5"><h2>{language !== 'fr' ? 'Loading...' : 'Chargement...'}</h2></Container>;
  if (error) return <Container className="my-5"><h2>{language !== 'fr' ? 'Loading error' : 'Erreur de chargement'}</h2></Container>;
  if (!data.team || data.team.length === 0) return <Container className="my-5"><h2>{language !== 'fr' ? 'No team member available' : 'Aucun membre disponible'}</h2></Container>;

  return (
    <Container className="my-5">
      <h1>{language !== 'fr' ? 'Our Team Members' : 'Nos Collaborateurs'}</h1>
      <br />
      <Row>
        {data.team.map((member, index) => (
          <Col md={4} key={index} className="mb-4">
            <TeamMemberCard
              member={member}
              isExpanded={expandedMember === index}
              onToggle={() => setExpandedMember(
                expandedMember === index ? null : index
              )}
            />
          </Col>
        ))}
      </Row>
    </Container>
  );
}

export default Team;